:root{--bg: #f5f7fa;--text: #1c1c1c;--accent: #00bcd4;--logo-color: #0099cc;--card-bg: #ffffff;--card-border: #e0e0e0;--logo-bg: transparent;--text-subtle: #6e6e6e;--link-color: #0d47a1}html.dark{--bg: #0f0f0f;--text: #f0f0f0;--accent: #00bcd4;--logo-color: #00f7ff;--card-bg: #1a1a1a;--card-border: #333333;--logo-bg: #ffffff;--text-subtle: #d7d7d7;--link-color: #64b5f6}*,*::before,*::after{box-sizing:border-box}html,body{width:100%;min-height:100%;margin:0;padding:0;overflow-x:hidden;font-family:sans-serif}.layout-container,.site-footer .footer-nav,.header__container{width:100%;max-width:1400px;margin-inline:auto;padding-inline:1rem}@media(max-width: 768px){.layout-container,.site-footer .footer-nav,.header__container{padding-inline:1.25rem}}@media(max-width: 480px){.layout-container,.site-footer .footer-nav,.header__container{padding-inline:1.5rem}}@media(max-width: 374px){.layout-container,.site-footer .footer-nav,.header__container{padding-inline:.25rem}}main{width:100%}main>*{max-width:100%;box-sizing:border-box}section{margin-bottom:3rem}.glove-cursor{cursor:url("/images/glove_pointer.png") 16 16,auto}.header{width:100vw;margin-left:calc(-50vw + 50%);background-color:var(--card-bg);border-bottom:1px solid var(--card-border);padding:1rem 0;position:relative;z-index:10}.header__container{display:flex;justify-content:space-between;align-items:center}@media(max-width: 768px){.header__container{flex-direction:column;gap:.5rem;text-align:center}}.header__logo{display:flex;align-items:center;gap:.5rem;font-family:"Orbitron",sans-serif;font-size:1.5rem;font-weight:bold;color:var(--logo-color);text-shadow:0 0 2px rgba(0,0,0,.5)}.header__logo a{display:flex;align-items:center;color:var(--logo-color);text-decoration:none;text-shadow:0 0 2px rgba(0,0,0,.5)}.header__logo a:hover{text-shadow:0 0 3px var(--logo-color),0 0 6px var(--logo-color)}.header__logo a img{width:40px;height:auto}.header__nav ul{display:flex;gap:2rem;list-style:none;padding:0;margin:0}@media(max-width: 768px){.header__nav ul{flex-wrap:wrap;justify-content:center}}.header__nav ul li a{position:relative;font-family:"Orbitron",sans-serif;font-weight:700;font-size:1.1rem;letter-spacing:.04em;color:var(--text);text-decoration:none;transition:color .3s ease}.header__nav ul li a:hover{color:var(--accent);text-shadow:0 0 1px var(--accent),0 0 1px var(--accent)}.header__nav ul li a::after{content:"";position:absolute;left:0;bottom:-4px;width:100%;height:2px;background:var(--accent);transform:scaleX(0);transform-origin:left;transition:transform .3s ease}.header__nav ul li a:hover::after{transform:scaleX(1)}.header__actions{display:flex;align-items:center;gap:1rem}@media(max-width: 768px){.header__actions{flex-wrap:wrap;justify-content:center}}.header__actions .toggle-group{display:flex;gap:.5rem}.header__actions .toggle-group button{width:32px;height:32px;display:grid;place-items:center;font-size:18px;background:none;border:1px solid var(--card-border);border-radius:4px;cursor:pointer;padding:0}.header__actions .toggle-group button.active,.header__actions .toggle-group button:hover{border-color:var(--accent)}.header__actions .toggle-group img{width:20px;height:20px;display:block}.site-footer{width:100vw;margin-left:calc(-50vw + 50%);background-color:var(--card-bg);color:var(--text);padding:1.5rem 0;text-align:center;font-family:"Orbitron",sans-serif;font-size:.95rem;border-top:1px solid var(--card-border);box-shadow:0 -1px 4px rgba(0,0,0,.05)}.site-footer .footer-nav{display:flex;justify-content:center;gap:2.5rem;flex-wrap:wrap;padding:.5rem}.site-footer .footer-nav a{position:relative;color:var(--text);text-decoration:none;font-weight:600;letter-spacing:.03em;transition:color .3s ease}.site-footer .footer-nav a::after{content:"";position:absolute;left:0;bottom:-3px;width:0%;height:2px;background-color:var(--accent);transition:width .3s ease}.site-footer .footer-nav a:hover{color:var(--accent)}.site-footer .footer-nav a:hover::after{width:100%}